define("appkit/app",
  ["resolver","appkit/router"],
  function(Resolver, router) {
    "use strict";

    var App = Ember.Application.create({
      LOG_ACTIVE_GENERATION: true,
      LOG_VIEW_LOOKUPS: true,
      modulePrefix: 'appkit',
      Resolver: Resolver,
      Router: Ember.Router.extend({
        router: router
      })
    });


    return App;
  });